import React from 'react';

import styled from 'styled-components';

interface SpinnerSVGProps {
    size?: number;
    thickness?: number;
}

const StyledSVG = styled.svg<SpinnerSVGProps>`
    width: ${({ size }) => size || 16}px;
    height: ${({ size }) => size || 16}px;
    stroke: ${({ theme }) => theme.palette.textHelp};
`;

const SpinnerSVG: React.FC<SpinnerSVGProps> = ({ size = 16, thickness = 1.5 }) => (
    <StyledSVG size={size} strokeWidth={thickness} viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
        <style>
            {`
                .spinnerRotation {
                    transform-origin: center;
                    animation: rotateSpinner 2s linear infinite;
                }
                .spinnerRotation circle {
                    stroke-linecap: round;
                    stroke-width: ${thickness};
                    animation: animateStroke 1.5s ease-in-out infinite;
                }
                @keyframes rotateSpinner {
                    100% {
                        transform: rotate(360deg);
                    }
                }
                @keyframes animateStroke {
                    0% {
                        stroke-dasharray: 0 150;
                        stroke-dashoffset: 0;
                    }
                    47.5% {
                        stroke-dasharray: 42 150;
                        stroke-dashoffset: -16;
                    }
                    95%, 100% {
                        stroke-dasharray: 42 150;
                        stroke-dashoffset: -59;
                    }
                }
            `}
        </style>
        <g className="spinnerRotation">
            <circle cx="12" cy="12" r="9.5" fill="none"></circle>
        </g>
    </StyledSVG>
);

export default SpinnerSVG;
