import { useCallback, useEffect, useRef, useState } from 'react';

import OpenFin from '@openfin/core';

interface DesktopAppSignal {
    appKey: string;
    title: string;
    icon?: string;
    selectedColorChannel?: string;
}

type UserDesktopAppSignal = Pick<DesktopAppSignal, 'appKey' | 'selectedColorChannel'>;

const ENTERPRISE_COMPONENT_BRIDGE_CHANNEL_NAME_SUFFIX = '-enterprise-component-bridge';

export const useAgentEventRouter = () => {
    const channelRef = useRef<OpenFin.ChannelClient | null>(null);
    const [isChannelInitialized, setIsChannelInitialized] = useState(false);
    const [isLoading, setIsLoading] = useState(true);
    const [originalDesktopAppSignals, setOriginalDesktopAppSignals] = useState<DesktopAppSignal[]>([]);
    const [desktopAppSignals, setDesktopAppSignals] = useState<DesktopAppSignal[]>([]);
    const [pendingUpdates, setPendingUpdates] = useState<UserDesktopAppSignal[]>([]);

    const onDesktopAppSignalsChanged = (payload: DesktopAppSignal[]) => {
        if (Array.isArray(payload)) {
            setOriginalDesktopAppSignals(payload);
            setDesktopAppSignals(payload);
            setPendingUpdates([]);
        } else {
            console.error('Invalid payload received for desktop app signals:', payload);
        }
    };

    const setColorChannel = useCallback(
        (appKey: DesktopAppSignal['appKey'], selectedColorChannel: DesktopAppSignal['selectedColorChannel']) => {
            if (!originalDesktopAppSignals.some((provider) => provider.appKey === appKey)) {
                console.error(`Provider with id ${appKey} not found`);
                return;
            }

            // Update the working copy
            setDesktopAppSignals((prevSignalProviders) =>
                prevSignalProviders.map((prevProvider) =>
                    prevProvider.appKey === appKey ? { ...prevProvider, selectedColorChannel } : prevProvider
                )
            );

            // Get the original color
            const originalDesktopAppSignal = originalDesktopAppSignals.find((p) => p.appKey === appKey);
            const originalColor = originalDesktopAppSignal?.selectedColorChannel;

            // Update pending updates
            setPendingUpdates((prevPendingUpdates) => {
                const filteredSignals = prevPendingUpdates.filter((prevUpdate) => prevUpdate.appKey !== appKey);

                // If the new color matches the original, remove from pending updates
                if (selectedColorChannel === originalColor) {
                    return filteredSignals;
                }

                // Otherwise add/update the pending change
                return [...filteredSignals, { appKey, selectedColorChannel }];
            });
        },
        [originalDesktopAppSignals]
    );

    const saveChanges = useCallback(async () => {
        if (isChannelInitialized && pendingUpdates.length > 0) {
            try {
                // Send all pending updates to the agent in a batch
                await channelRef.current?.dispatch(
                    'set-desktop-app-signals',
                    desktopAppSignals.map((provider) => ({
                        appKey: provider.appKey,
                        selectedColorChannel: provider.selectedColorChannel
                    }))
                );

                // Update the original signals to match the saved state
                setOriginalDesktopAppSignals(desktopAppSignals);
            } catch (error) {
                console.error('Failed to save desktop app signals:', error);
                return false;
            }

            // Clear pending updates after saving
            setPendingUpdates([]);
        }

        return true;
    }, [pendingUpdates, isChannelInitialized, desktopAppSignals]);

    useEffect(() => {
        if (isChannelInitialized) {
            (async () => {
                // Fetch the initial state of signal providers
                const payload = await channelRef.current?.dispatch('get-desktop-app-signals');
                onDesktopAppSignalsChanged(payload);
                setIsLoading(false);
            })();
        }
    }, [isChannelInitialized]);

    useEffect(() => {
        (async () => {
            if (!channelRef.current) {
                channelRef.current = await fin.InterApplicationBus.Channel.connect(
                    `${fin.me.identity.uuid}${ENTERPRISE_COMPONENT_BRIDGE_CHANNEL_NAME_SUFFIX}`,
                    {
                        wait: true
                    }
                );
                channelRef.current.setDefaultAction(() => false);

                setIsChannelInitialized(true);
            }
        })();

        return () => {
            if (channelRef.current) {
                channelRef.current.disconnect();
                channelRef.current = null;
            }
        };
    }, []);

    return { isLoading, desktopAppSignals, pendingUpdates, setColorChannel, saveChanges };
};
